    align-items: center;
    justify-content: space-evenly;
    margin-top: 30px;
    flex-wrap: wrap;
}

.burger{
    display: block;
}

.navigation{
    display: none;
}

.sci{
    display: none;
}

.penyuluhan{
    color: #fff;
    padding: 2rem 10%;
}

.penyuluhan .title{
    text-align: center;
    color: black;
}

.penyuluhan .title h3{
    font-size: 30px;
}

.penyuluhanContent .coll1{
    margin-right: 30px;
    width: 50%;
}

.penyuluhanContent .coll2{
    width: 50%;
}

.penyuluhanContent .coll1 img,
.penyuluhanContent .coll2 img{
    width: 100%;
    border-radius: 20px;
}

.penyuluhanContent .coll2 .card2{
    margin-top: 30px;
}

.penyuluhanContent h4{
    margin-bottom: 10px;
    font-size: 15px;
}

.penyuluhanContent p{
    font-size: 12px;
}

.cardContent .reduce img{
    width: 100px;
}

.cardContent .reuse img{
    width: 100px;
}

.cardContent .recycle img{
    width: 100px;
}



/* Trash */
.trash{
    padding: 2rem 10%;
}

.trash .title h3{
    font-size: 30px;
}

.trashContent{
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.organik .cardContent {
    display: flex;
    align-items: center;
    justify-content: space-around;
    
}

.galleryContent{
    display: flex;
    align-items: center;
    justify-content:space-between;
    margin-top: 20px;
    text-align: center;
    flex-wrap: wrap;
    row-gap: 25px;
}

.trash button{
    margin-left: 50px;
}


