    flex-direction: column;
    justify-content: space-between; /* Distribute space evenly */
}


.galleryContent .card{
    width: 100%;
}

.galleryContent{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    text-align: center;
    flex-wrap: wrap;
}

.gallery .images,
.gallery .images img{
    width: 100%;
}

/* Login */
.left-content {
    flex: 0;
    display: flex;
    justify-content: center; /* Mengatur gambar di tengah */
}

.main_login {
    display: flex;
    padding: 4.4% 30px;
    margin: 20px;
    /* background-color: red; */
}

.right-content-Login{
