.trashDetails .card{
    flex-wrap: wrap;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 10px;
}

.trashDetails{
    margin: 30px 0;
}

.trashDetails .images{
    width: 100%;
    margin-right: 0;
    height: auto;
}

.trashDetails .images img{
    width: 100%;
    height: auto;
}

