    display: flex;
    flex-direction: column;
}
}

/* Media 830 */
@media screen and (max-width: 830px){
    /* LAPORAN */
    .formLaporan .cards {
         margin: 2% 2%;
    }
}


/* Media 800 */
@media screen and (max-width: 800px){
    /* TENTANG KAMI */
    .team .ourTeam{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 40px;
        flex-wrap: wrap;
        gap: 10px;
        row-gap: 30px;
    }
}

