    padding: 16px;
    width: 500px; 
    margin: 0 60px; 
}

.right-content-Login p{
    text-align: center;
    margin: 5px;
    padding: 4px;
}

.right-content-Login p a{
   text-decoration: underline;
   color: #3F18D8;
}

.image-animation {
    height: 400px;
    width: 400px;
    display: block;
    max-width: 100%;
    margin: 0 auto;
    /* Tambahkan margin untuk menggeser gambar */
    margin-left: 20px; /* Geser gambar ke kanan */
    margin-right: 50px; 
}

.left-content {
    flex: 4;
    display: flex;
    justify-content: center; /* Mengatur gambar di tengah */
}

.right-content-Login #login{
    font-size: 14px;
    background-color: white;
    color: black;
    border: 1px solid black;
    border-radius: 6px;
    display: block;
    width: 50%;
    padding: 12px;
    cursor: pointer;
    margin: 16px auto 0; 
    align-items: center;
    text-align: center; 
    /* background-color: #3F18D8; */
}

.right-content-Login #login:hover {
    font-weight:bolder;
    transition: .5s ease-in-out;
}
.right-content-Login > h2{
    text-align: center;
    /* background-color: tomato; */
    margin-top: 10px;
    padding: 10px;
    font: 28px;
}

.right-content-Login > form > .input{
    margin: 8px 30px;
}

.right-content-Login > form > .input > input {
    display: block;
    width: 100%;
    padding: 12px;
    border-radius: 6px;
    margin: 25px auto; 
    margin-block-end: 2rem;
    /* background-color: yellow; */
}

.input label{
    font-size: 20px;
}

.footerLogin{
    position: absolute;
}

/* Login End */

/* Proof Upload Start */
.ProofUpload {
    padding: 20px;
}

.uploadDetails {
    margin-bottom: 30px;
}

.uploadCard {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    overflow: hidden;

    /* background-color: red; */
}

.uploadCard .imagesUpload img {
    width: 400px;
    height: auto;
    display: block;
}

.uploadCard .uploadCardText {
    padding: 20px;
    flex-grow: 1;
    position: relative;
    text-align: justify;

    /* background-color: red; */
}

.uploadCard .header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.uploadCard .location {
    margin: 0;
    color: black;
    font-size: 15px;

    /* background-color: red; */
}

.thankYouSection {
    text-align: center;
    background-color: #f5f5f5;
    padding: 20px;
    border-radius: 5px;
}

.thankYouSection h1 {
    margin-top: 0;
    color: #264e36;
}

.uploadContainer {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin: 20px 0;
}

.uploadBox {
    width: 100px;
    height: 100px;
    border: 2px dashed #aaa;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    color: #aaa;
    cursor: pointer;
    overflow: hidden; /* Agar gambar tidak terlalu besar */
}

.uploadBox img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Agar gambar sesuai dengan ukuran div */
}

.uploadDeskripsi {
    width: 100%;
    height: 100px;
    margin-bottom: 20px;
    padding: 10px;
    border: 2px solid black;
    border-radius: 5px;
    resize: none;
}

.thankYouSection button {
    background-color: #4f6e52;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}
/* Proof Upload End */

/* Tentang Kami */
.team{
    padding: 2rem 5%;
}

.team .title h1{
    text-align: center;
    font-size: 35px;
}

.team .ourTeam{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 40px;
    flex-wrap: wrap;
    gap: 20px;
    row-gap: 30px;
}

.team .ourTeam .card{
    width: 350px;
    text-align: justify;
    border: 1px solid gray;
}

.team .ourTeam .cardText{
    padding: 10px;
}

.team .ourTeam .card .images{
    width: 100%;
    height: 400px;
}

.team .ourTeam .card img{
    width: 100%;
    height: 100%;
}


/* End Tentang Kami */

/* visi */
.visi{
    padding: 2rem 5%;
    }
    
.visiContent{
    background-color: lightgray;
    padding: 20px;
    margin-top: 15px;
}

.misi {
    padding: 2rem 5%;
}

.misiContent{
    background-color: lightgray;
    padding: 20px 40px;
    margin-top: 15px;
}
/* End Visi */

/* Start Hasil */
.hasil {
    padding: 2rem 2%;
    background-color: #f4f4f4;
    
    display: flex;
    justify-content: space-around;
    align-items: left;
}

#hasil{
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    row-gap: 40px;
}

.teksTitle {
    text-align: center;
    padding: 1rem;
    background-color: #f4f4f4;
}

.teksTitle h3 {
    font-size: 28px;
    color: #4F6F52;
    text-transform: uppercase;
}


/* CSS untuk bagian .hasilContent */
.hasilContent {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    
    justify-content: space-between;
    align-items: flex-start;
}

.hasilContent a {
    text-decoration: none;
    color: inherit;
}

.hasilContent .cardResult {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    width: 330px;
    display: flex;
    flex-direction: column;
    /* background-color: red; */
}

.hasilContent .cardResult:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 10px rgba(79, 111, 82, 0.8);
}

/* CSS untuk bagian gambar .imagesResult */
.hasilContent .cardResult .imagesResult {
    width: 100%;
    height: 150px; /* Atur tinggi yang sesuai untuk gambar */
    overflow: hidden;
}

.hasilContent .cardResult .imagesResult img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

/* CSS untuk bagian teks .teksDesc */
.hasilContent .cardResult .teksDesc {
    padding: 15px;
}

.hasilContent .cardResult .teksDesc p:first-child {
    font-size: 14px;
    color: #777;
}

.hasilContent .cardResult .teksDesc h3 {
    font-size: 18px;
    color: #333;
    margin: 10px 0;
}

.hasilContent .cardResult .teksDesc p:last-child {
    font-size: 14px;
    color: #555;
    line-height: 1.5;
    /* Memotong teks yang terlalu panjang dengan elipsis */
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 60px; /* Sesuaikan tinggi agar sesuai dengan jumlah baris yang diinginkan */
    text-align: justify;
}
/* End Hasil */

/* Halaman Verifikasi */
.login-otp{
    background: white;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    width: 100%;
    text-align: center;
    height: 76.4vh;
}

.wrap_otp h2 {
    margin-bottom: 20px;
    color: #333;
    font-size: 24px;
    font-weight: bold;
}

.wrap_otp{
  padding: 3rem 25%;
}

.wrap_otp #formulir-otp .input{
    margin-bottom: 15px;
}

.wrap_otp #formulir-otp  input{
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-sizing: border-box;
}

.wrap_otp #formulir-otp input:focus{
    border-color:#4F6F52;
    outline: none;
}

.wrap_otp #formulir-otp button{
    width: 100%;
    padding: 10px 12px;
    font-size: 18px;
    color: black;
    background: #d3d3d3;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bolder;
    transition: 0.3s ease;
}

.wrap_otp #formulir-otp button:hover{
    background-color:#b0b0b0;
}


/* icon */
.input .iconUserLogin {
    position: absolute;
    right: 230px;
    top: 293px;
    height: 30px;
    width: 30px;
}

.input .iconUserPassword {
    position: absolute;
    right: 230px;
    top: 370px;
    height: 30px;
    width: 30px;
}

/* END Halaman Verifikasi */

