CnB,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,wBAAwB;AAC5B;;;AAGA,SAAS;AACT;IACI,kBAAkB;IAClB,YAAY;IACZ,UAAU;IACV,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,UAAU;IACV,YAAY;IACZ,WAAW;AACf;;AAEA,2BAA2B","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');\r\n@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap');\r\n\r\n*{\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\na{\r\n    text-decoration: none;\r\n    color: black;\r\n}\r\n\r\nbody{\r\n    font-family: \"Poppins\", sans-serif;\r\n}\r\n\r\n\r\nheader .navbar{\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 2rem 5%;\r\n    background-color: #4f6e52;\r\n}\r\n\r\n\r\nheader .navigation ul li{\r\n    display: inline-block;\r\n    margin-right: 20px;\r\n}\r\n\r\nheader .navigation a,\r\nheader h3,\r\nheader i{\r\n    color: white;\r\n}\r\n\r\n.sci {\r\n    position: relative;\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n\r\n.sci a {\r\n    color: white;\r\n    text-decoration: none;\r\n    font-size: 18px;\r\n}\r\n\r\n#userFullName{\r\n    color: white;\r\n    margin-left: 15px;\r\n}\r\n\r\n.dropdown {\r\n    display: none;\r\n    position: absolute;\r\n    border: 1px solid black;\r\n    border-radius: 8px;\r\n    top: 25px;\r\n    left: -68px;\r\n    background-color: white;\r\n    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);\r\n    z-index: 2;\r\n    flex-direction: row;\r\n    width: 150px;\r\n    /* padding-bottom: 20px; */\r\n    /* background-color: yellow; */\r\n}\r\n\r\n.dropdown a {\r\n    color: black;\r\n    padding: 8px 16px;\r\n    text-decoration: none;\r\n    display: block;\r\n    text-align: center;\r\n}\r\n\r\n.hiddenElement{\r\n    display: none;\r\n}\r\n\r\n.dropdown a:hover {\r\n    background-color: #ddd;\r\n    border-radius: 8px;\r\n    transition: 0.3s ease-in-out;\r\n}\r\n\r\n\r\nspan{\r\n    color: black;   \r\n}\r\n\r\n.burger{\r\n    display: none;\r\n}\r\n\r\n.burger .bars{\r\n    background-color: white;\r\n    width: 30px;\r\n    height: 3px;\r\n    margin-top: 5px;\r\n}\r\n\r\n.sidebar{\r\n    background-color: #fff;\r\n    position: absolute;\r\n    top: 0;\r\n    left: -1000px;\r\n    height: 100%;\r\n    width: 50%;\r\n    padding: 2rem 5%;\r\n    z-index: 99;\r\n}\r\n\r\n.sidebar ul li{\r\n    margin-top: 10px;\r\n    list-style: none;\r\n}\r\n\r\n.sidebar.active{\r\n    left: 0;\r\n}\r\n\r\n/* Css untuk bagian carousel */\r\n.hero{\r\n    height: 500px;\r\n}\r\n\r\n.hero .card1{\r\n    background-image: url(../public/images/carousel/carousel1.png);\r\n    height: 500px;\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    display: flex;\r\n    align-items: center;\r\n    padding: 2rem 5%;\r\n    background-position: bottom;\r\n}\r\n\r\n.hero .card2{\r\n    background-image: url(../public/images/carousel/carousel2.png);\r\n    height: 500px;\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    display: flex;\r\n    align-items: center;\r\n    padding: 2rem 5%;\r\n    background-position: bottom;\r\n}\r\n\r\n.hero .card3{\r\n    background-image: url(../public/images/carousel/carousel3.png);\r\n    height: 500px;\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    display: flex;\r\n    align-items: center;\r\n    padding: 2rem 5%;\r\n    background-position: bottom;\r\n}\r\n\r\n.hero .heroText{\r\n    color: #fff;\r\n}\r\n\r\n.heroText h1{\r\n    font-size: 50px;\r\n}\r\n\r\n.heroText p{\r\n    font-size: 25px;\r\n}\r\n\r\n/* Hero CSS end */\r\n\r\n/* Tombol navigasi start */\r\n.swiper-button-next,\r\n.swiper-button-prev {\r\n    color: white !important; /* Warna tombol */\r\n    width: 50px;\r\n    height: 50px;\r\n    margin-top: -25px; /* Posisikan di tengah vertikal */\r\n    /* background-color: red; */\r\n}\r\n\r\n.swiper-button-next {\r\n    right: 10px;\r\n}\r\n\r\n.swiper-button-prev {\r\n    left: 10px;\r\n}\r\n\r\n.swiper-button-next::after,\r\n.swiper-button-prev::after {\r\n    font-size: 20px; /* Ukuran ikon panah */\r\n}\r\n\r\n/* Tombol navigasi end */\r\n\r\n/* Penyuluhan */\r\n.penyuluhanContent{\r\n    display: flex;\r\n    background: linear-gradient(rgba(168, 205, 159, 0.9), rgba(79, 111, 82, 0.8));\r\n    padding: 2rem;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin-top: 30px;\r\n    border-top-left-radius: 20px;\r\n    border-top-right-radius: 20px;\r\n}\r\n\r\n.penyuluhan{\r\n    color: #fff;\r\n    padding: 2rem 10%;\r\n}\r\n\r\n.penyuluhan .title{\r\n    text-align: center;\r\n    color: black;\r\n}\r\n\r\n.penyuluhan .title h3{\r\n    font-size: 30px;\r\n}\r\n\r\n.penyuluhanContent .coll1{\r\n    margin-right: 30px;\r\n    width: 50%;\r\n}\r\n\r\n.penyuluhanContent .coll2{\r\n    width: 50%;\r\n}\r\n\r\n.penyuluhanContent .coll1 img,\r\n.penyuluhanContent .coll2 img{\r\n    width: 100%;\r\n    border-radius: 20px;\r\n}\r\n\r\n.penyuluhanContent .coll2 .card2{\r\n    margin-top: 30px;\r\n}\r\n\r\n.penyuluhanContent h4{\r\n    margin-bottom: 10px;\r\n    font-size: 15px;\r\n}\r\n\r\n.penyuluhanContent p{\r\n    font-size: 12px;\r\n}\r\n\r\n/* Trash */\r\n.trashTitle{\r\n    padding: 2rem 10%;\r\n    font-size: 30px\r\n}\r\n\r\n.trash{\r\n    padding: 2rem 7%;\r\n    display: flex;\r\n    justify-content: space-around;\r\n    row-gap: 2rem;\r\n    align-items: left;\r\n    flex-flow: row wrap;\r\n}\r\n\r\n.trashContent{\r\n    margin: 20px;\r\n    display: flex;\r\n    align-items: flex-start;\r\n    justify-content: space-between;\r\n    flex-wrap: wrap;\r\n    row-gap: 20px;\r\n    text-align: justify;\r\n}\r\n\r\n.trashContent img {\r\n    border-radius: 10px;\r\n    width: 100%; /* Pastikan gambar memenuhi lebar kontainer */\r\n    height: 140px; /* Biarkan tinggi menyesuaikan agar gambar tidak terdistorsi */\r\n    max-height: 200px; /* Batasi tinggi maksimal gambar */\r\n    max-width: 100%; /* Batasi lebar maksimal gambar */ \r\n}\r\n\r\n.trashContent .card{\r\n    width: 350px;\r\n}\r\n\r\n.trash .trashContent h3{\r\n    margin: 5px 0;\r\n}\r\n\r\n.trash button{\r\n    margin-top: 50px;\r\n    border-radius: 20px;\r\n    padding: 10px 20px;\r\n    border: none;\r\n    cursor: pointer;\r\n}\r\n\r\n.trashContent .card .cardText p:last-child {\r\n    font-size: 14px;\r\n    color: #555;\r\n    line-height: 1.5;\r\n    /* Memotong teks yang terlalu panjang dengan elipsis */\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 3;\r\n    -webkit-box-orient: vertical;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    height: 60px; /* Sesuaikan tinggi agar sesuai dengan jumlah baris yang diinginkan */\r\n    text-align: justify;\r\n}\r\n\r\n/* Trash Home Display */\r\n.homeTrashTitle{\r\n    padding: 0rem 10%;\r\n    margin-top: 40px;\r\n    font-size: 30px;\r\n}\r\n\r\n.homeTrash{\r\n    padding: 2rem 10%;\r\n    display: flex;\r\n    gap: 5px;\r\n    align-items: flex-start;\r\n    flex-flow: row wrap;\r\n}\r\n\r\n.trashHomeDisplay{\r\n    /* margin: 20px; */\r\n    display: flex;\r\n    align-items: flex-start;\r\n    justify-content: space-evenly;\r\n    flex-wrap: wrap;\r\n    row-gap: 20px;\r\n    border-radius: 10px;\r\n    flex: 1;\r\n    min-width: 250px\r\n    /* background-color: red; */\r\n}\r\n\r\n.trashHomeDisplay img {\r\n    border-radius: 10px;\r\n    width: 100%; /* Pastikan gambar memenuhi lebar kontainer */\r\n    height: 140px; /* Biarkan tinggi menyesuaikan agar gambar tidak terdistorsi */\r\n    max-height: 200px; /* Batasi tinggi maksimal gambar */\r\n    max-width: 100%; /* Batasi lebar maksimal gambar */ \r\n}\r\n\r\n.trashHomeDisplay .homeCardTrash{\r\n    width: 250px;\r\n}\r\n\r\n.homeTrash .trashHomeDisplay h3{\r\n    margin: 5px 0;\r\n    font-size: 15.3px;\r\n}\r\n\r\n.HomeTrashButton{\r\n    padding: 2rem 10%;\r\n}\r\n\r\n.HomeTrashButton button{\r\n    border-radius: 20px;\r\n    padding: 10px 20px;\r\n    border: none;\r\n    cursor: pointer;\r\n}\r\n\r\n.trashHomeDisplay .homeCardTrash .cardTextHomeDisplay p:last-child {\r\n    font-size: 14px;\r\n    color: #555;\r\n    line-height: 1.5;\r\n    /* Memotong teks yang terlalu panjang dengan elipsis */\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 3;\r\n    -webkit-box-orient: vertical;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    height: 60px; /* Sesuaikan tinggi agar sesuai dengan jumlah baris yang diinginkan */\r\n    text-align: justify;\r\n}\r\n/* End Trash Home Display */\r\n\r\n/* Recyle */\r\n.recyle{\r\n    padding: 2rem 10%;\r\n    color: #fff;\r\n}\r\n\r\n.recyle .card{\r\n    background: linear-gradient(#A8CD9F, rgba(79, 111, 82, 0.8));\r\n    padding: 2rem 5%;\r\n    text-align: center;\r\n    border-bottom-left-radius: 20px;\r\n    border-bottom-right-radius: 20px;\r\n}\r\n\r\n.cardContent .reduce{\r\n    padding: 30px;\r\n    margin: 0 20px;\r\n}\r\n\r\n.cardContent .reuse{\r\n    padding: 30px;\r\n    margin: 0 20px;\r\n}\r\n\r\n.cardContent .recycle{\r\n    padding: 30px;\r\n    margin: 0 20px;\r\n}\r\n\r\n.recyle .cardContent{\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    margin-top: 30px;\r\n}\r\n\r\n.recyle .cardContent i{\r\n    font-size: 30px;\r\n}\r\n\r\n.recyle .cardContent h3{\r\n    font-size: 15px;\r\n    margin-top: 20px;\r\n}\r\n\r\n/* Features */\r\n.features {\r\n    padding: 2rem 10%;\r\n}\r\n\r\n.features .title h3 {\r\n    text-align: center;\r\n    font-size: 40px;\r\n}\r\n\r\n.featuresContent {\r\n    margin-top: 30px;\r\n    display: flex;\r\n    align-items: center; /* Ensure all cards have the same height */\r\n    justify-content: space-around;\r\n    flex-wrap: wrap;\r\n    gap: 20px; /* Add gap between cards if necessary */\r\n}\r\n\r\n.features .card {\